                        if(res.jyltbt[1][i].dm){
                            _this.baroption4.dm.push(res.jyltbt[1][i].dm)
                        }
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption4.xAxis[0].data = _this.xData;
                    _this.baroption4.series[0].data = _this.yData;
                }
                if(res.hzqytbt){
                    _this.xData = []
                    _this.yData = []
                    _this.baroption5.param.push(res.hzqytbt[0].param1)
                    for(var i = 0;i < res.hzqytbt[1].length;i++){
                        _this.xData.push(res.hzqytbt[1][i].mc)
                        _this.yData.push(res.hzqytbt[1][i].zcount)
                        if(res.hzqytbt[1][i].dm){
                            _this.baroption5.dm.push(res.hzqytbt[1][i].dm)
                        }
                    }
                    // this.baroption1.yAxis[0].name = '㎡'
                    _this.baroption5.xAxis[0].data = _this.xData;
                    _this.baroption5.series[0].data = _this.yData;
                }
                _this.chart = true
            },
            error:function(err){
                console.log(err)
            }
        });
    },
}
</script>

<style>
#xsgz{
    width: 100%;
    height: 100%;
}
#xsgz .row{
    display:flex;
    justify-content:center;
}
#xsgz .row .col{
    width:750px;
    margin-top:20px;
}
#xsgz .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#xsgz .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#xsgz .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#xsgz .shaixuan_box{
    width: 400px;
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #xsgz .shaixuan_box{
        left: 3%!important;
    }
}
#xsgz .row .tipheight{
    height:435px;
}
#xsgz .row .tip_select{
    height:465px;
}
#xsgz .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#xsgz .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#xsgz .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#xsgz .is-top :hover{
    color: #029348 !important;
}
#xsgz .is-left :hover{
    color: #029348 !important;
}
#xsgz .el-card{
    padding: 40px 0 40px 0;
}
/* #xsgz .shaixuan{
    padding: 82px 0 32px 0;
} */
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>




